import React, { useContext, useEffect } from "react";
import { Link } from "react-router-dom";
import { StoreContext } from "../../store/StoreProvider";

const ShoppingCart = (ordered) => {
  const { cartStatus, setCartStatus, isLoggedIn, setGuest, setInCheckout } =
    useContext(StoreContext);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const prices = [];
  cartStatus.map((i) => prices.push(i.price));
  const pricesSum = prices.reduce((a, b) => a + b, 0);

  const cartItems = () => {
    if (cartStatus.length > 0) {
      const itemBodies = cartStatus.map((i) => {
        return (
          <div className="col-md-10 mx-auto">
            <div className="card mb-2 flex-row justify-content-center align-items-center">
              <img
                className="card-img-top p-1"
                style={{ width: "70px" }}
                src={`/images/${i.category}.jpg`}
                alt={i.title}
              />
              <div className="card-body">
                <div className="d-flex ">
                  <p className="card-title mb-0 flex-grow-1 d-flex align-items-center font-weight-bold">
                    {i.title}
                  </p>
                  <div className="d-flex align-items-center justify-content-center flex-column">
                    <p className="mx-0 mb-0 d-flex align-items-center">
                      {i.price.toFixed(2)}$
                    </p>
                    <button
                      className="btn btn-danger m-0 p-1"
                      data-value={i.id}
                      onClick={(e) => {
                        const itemId = e.target.getAttribute("data-value");
                        const itemIndex = cartStatus.findIndex(
                          (i) => i.id == itemId
                        );
                        let newList = [...cartStatus];
                        newList.splice(itemIndex, 1);
                        setCartStatus(newList);
                      }}
                    >
                      Usuń
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        );
      });
      return itemBodies;
    } else {
      return (
        <div className="m-5">
          <p className="text-center">
            {" "}
            Brak produktów w koszyku. Przejdź do sklepu i wybierz coś dla
            siebie.
          </p>
        </div>
      );
    }
  };
  console.log(ordered.ordered ? "Koszyk" : "Wybrane produkty");

  const summaryPanel = (
    <div className="col-md-12">
      <div className="card mb-2 flex-row justify-content-center align-items-center">
        <div className="card-body flex-row">
          <p className="card-title">
            łączna kwota{" "}
            <span>{(Math.round(pricesSum * 100) / 100).toFixed(2)}</span>{" "}
          </p>
          <Link
            to={isLoggedIn ? "/checkout/order" : "/welcome"}
            className="btn btn-outline-success"
            onClick={() => {
              !isLoggedIn && setGuest(true);
              setInCheckout(true);
            }}
          >
            Przejdź do dostawy
          </Link>
        </div>
      </div>
    </div>
  );
  return (
    <div className="container" style={{ minHeight: "50vh" }}>
      <div className="my-3">
        <p className="font-weight-bold text-center text-uppercase">
          {ordered.ordered ? "Wybrane produkty" : "Koszyk"}
        </p>
      </div>
      {cartItems()}
      {!ordered.ordered && cartStatus.length > 0 && summaryPanel}
    </div>
  );
};

export default ShoppingCart;
