import React, { useContext, useEffect } from "react";
import { useHistory, useLocation } from "react-router";
import { StoreContext } from "../../../store/StoreProvider";

import { paymentMethods } from "../../../store/StoreProvider";
import ShoppingCart from "../../ShoppingCart/ShoppingCart";

const Summary = () => {
  const {
    setCheckoutStep,
    guestData,
    activeUser,
    setCartStatus,
    setOrders,
    orders,
  } = useContext(StoreContext);
  const location = useLocation();
  const history = useHistory();

  useEffect(() => {
    window.scrollTo(0, 0);
    setCheckoutStep(3);
  }, []);

  useEffect(() => {
    if (Boolean(!guestData) && Boolean(!activeUser)) {
      history.push("/error");
      console.log(Boolean(!guestData) && Boolean(!activeUser));
    }
  }, []);

  useEffect(() => {}, []);
  const userData = activeUser;
  const guestInfoData = guestData;
  const orderInfo = location.state.order;
  const { id, dateOfOrder, items, user, price, delivery, payment } = orderInfo;
  console.log(orders, orderInfo);

  const handleAcceptButton = () => {
    setCartStatus([]);
    setOrders(orders.concat(orderInfo));
    history.push({
      pathname: "/checkout/success",
      state: {
        order: orderInfo,
      },
    });
  };

  const handleChangeDataButton = () => {
    history.push("/checkout/order");
  };

  const body = (
    <div>
      <h3>Podsumowanie</h3>
      <div className="row justify-content-center">
        <div className="col-md-6 m-2 p-3 bg-light">
          <p className="font-weight-bold border-bottom border-dark pb-2">
            Dane osobowe
          </p>
          <div>
            <p className="font-weight-bold m-0 pt-3 border-bottom">
              {userData
                ? userData.nameAndSurname
                : guestInfoData.nameAndSurname}
            </p>
            <small className="text-muted">Imię i nazwisko</small>
            <p className="font-weight-bold m-0 pt-3 border-bottom">
              {userData ? userData.adress : guestInfoData.adress}
            </p>
            <small className="text-muted">Adres</small>
            <p className="font-weight-bold m-0 pt-3 border-bottom">
              {userData ? userData.postCodeCity : guestInfoData.postCodeCity}
            </p>
            <small className="text-muted">Adres pocztowy i miejscowość</small>
            <p className="font-weight-bold m-0 pt-3 border-bottom">
              {userData ? userData.phone : guestInfoData.phone}
            </p>
            <small className="text-muted">Numer telefonu dla kuriera</small>
          </div>
        </div>
        <div className="col-md-4 m-2 row ">
          <div className="col-md-12 p-3 bg-light mb-2">
            <p className="font-weight-bold border-bottom border-dark pb-2">
              Dostawa
            </p>
            <p>{delivery}</p>
          </div>
          <div className="col-md-12 p-3 bg-light mt-2">
            <p className="font-weight-bold border-bottom border-dark pb-2">
              Sposób płatności
            </p>
            <p>{paymentMethods[payment - 1]}</p>
          </div>
        </div>
      </div>
      <div className="col-md-10 text-center mx-auto">
        <button className="btn btn-success" onClick={handleChangeDataButton}>
          Edytuj dane
        </button>
      </div>
      <div className="col-md-10 mx-auto bg-light">
        <ShoppingCart ordered={true} />
      </div>
      <div className="col-md-12 bg-light pt-3 mt-3">
        <div className="row">
          <div className="col-md-9 mx-auto">
            <div className="d-flex">
              <p className="flex-grow-1">Wartośc koszyka</p>
              <p>{price} $</p>
            </div>
            <div className="d-flex justify-content-around">
              <p className="flex-grow-1">Dostawa</p>
              <p>0.00 $</p>
            </div>
            <div className="d-flex justify-content-around border-bottom border-dark mb-4">
              <p className="flex-grow-1">Płatność</p>
              <p>0.00 $</p>
            </div>
            <div className="d-flex justify-content-around font-weight-bold">
              <p className="flex-grow-1">Do zapłaty</p>
              <p>{price} $</p>
            </div>
            <div className="text-center m-4">
              <button
                onClick={handleAcceptButton}
                className="btn btn-success w-75"
              >
                Przejdź dalej{" "}
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  return <>{body}</>;
};

export default Summary;
