import React, { useContext, useEffect, useHistory } from "react";
import { Link } from "react-router-dom";
import { StoreContext } from "../../../store/StoreProvider";
import LoginPanel from "./LoginPanel";

const WelcomePanel = () => {
  const { guest, setGuest } = useContext(StoreContext);
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  // const history = useHistory();

  const infoPanel = (
    <div className="col-md-10 m-2 bg-light">
      <div>
        <h3 className="text-center p-3 ">
          Dlaczego warto mieć konto w MainShop
        </h3>
        <div className="row">
          <div className="col-md-6 p-3">
            <p className="text-center">
              <i class="fa fa-fighter-jet"></i>
            </p>
            <p className="text-center"> Zamawiaj szybciej !</p>
          </div>
          <div className="col-md-6 p-3">
            <p className="text-center">
              <i class="fa fa-list-ol"></i>
            </p>
            <p className="text-center"> Sprawdzaj poprzednie zamówienia</p>
          </div>
          <div className="col-md-6 p-3">
            <p className="text-center">
              <i class="fa fa-car-side"></i>
            </p>
            <p className="text-center"> Śledź status zamówienia</p>
          </div>
          <div className="col-md-6 p-3">
            <p className="text-center">
              <i class="fa fa-percent"></i>
            </p>
            <p className="text-center"> Korzystaj z rabatów i promocji</p>
          </div>
        </div>
      </div>
    </div>
  );
  return (
    <div className="container">
      <h3 className="text-center m-3">Witaj !</h3>
      <div className="row row justify-content-md-center">
        <LoginPanel />
        <div className="col-md-5 m-2 bg-light p-4">
          <div className="d-flex justify-content-center flex-column m-auto">
            <h3 className="text-center my-5">Nie masz jeszcze konta ?</h3>
            <Link
              to="/register"
              class="btn btn-primary w-50 m-auto"
              onClick={() => {
                setGuest(false);
              }}
            >
              Zarejestruj się
            </Link>
          </div>
        </div>
        {guest && (
          <div className="col-md-10 text-center m-3 p-3 bg-light">
            <p>Kontynuuj jako gość</p>
            <Link
              to="/register"
              onClick={() => {
                setGuest(true);
              }}
              class="btn btn-primary w-50 m-auto"
            >
              Dalej
            </Link>
          </div>
        )}
        {infoPanel}
      </div>
    </div>
  );
};

export default WelcomePanel;
